.header {
    padding: 15px;
    background: linear-gradient(102.48deg, #976464 -29.03%, #CFA5A5 97.75%);
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    text-transform: uppercase;
    color: #FFFFFF;

    &__wrp {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__btns {
        display: flex;
        gap: 10px;
    }
}